<template>
  <div class="all-user">
      <div style="margin:20px">
        <el-button type="info" @click="userFreez"></el-button>
        <el-button type="danger" @click="userDelete"></el-button>
        <el-button type="primary" @click="setAuthority"></el-button>
        <el-dialog title="" :visible.sync="authorityDialog">
            <el-form>
              <el-form-item label="" label-width="120px" style="text-align: left;">
                <el-select v-model="desAuthority" placeholder="">
                  <el-option label="" value=4></el-option>
                  <el-option label="" value=3></el-option>
                  <el-option label="" value=2></el-option>
                  <el-option label="" value=1></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="authorityDialog = false"> </el-button>
              <el-button type="primary" @click="handleSet"> </el-button>
            </div>
          </el-dialog>
      </div>
          <el-table ref="multipleTable" :data="userTable" tooltip-effect="dark" @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column label="" width="220" prop="name">
              </el-table-column>
              <el-table-column prop="email" width="220" label="" show-overflow-tooltip>
              </el-table-column>
              <el-table-column prop="tag" label="" width="200" :filters="[{text:'',value:''},{text:'',value:''},{text:'',value:''},{text:'',value:''}]" :filter-method="filterTag" filter-placement="bottom-end">
                  <template slot-scope="scope">
                      <el-tag :type="scope.row.tag==='boss' ? 'primary' : 'success'" close-transition>{{scope.row.tag}}</el-tag>
                  </template>
              </el-table-column>
              <el-table-column label="" width="220" prop="state">
              </el-table-column>
          </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "boss",
      authorityDialog: false,
      desAuthority: "",
      userTable: [
        { name: "tom", email: "10068@qq.com", tag: "", state: "" },
        {
          name: "shaun",
          email: "10068@qq.com",
          tag: "",
          state: ""
        },
        {
          name: "jim",
          email: "10068@qq.com",
          tag: "",
          state: ""
        },
        { name: "jacm", email: "10068@qq.com", tag: "", state: "" },
        { name: "tom", email: "10068@qq.com", tag: "", state: "" }
      ],
      operationList: []
    };
  },
  created() {
    this.reqAllUser("/userManagement");
  },
  methods: {
    handleSelectionChange: function(selection) {
      this.operationList = selection;
      console.log(this.operationList);
    },
    filterTag: function(value, row) {
      return row.tag === value;
    },
    userFreez: function() {
      var posttable = { emailList: [] };
      for (var i = 0; i < this.operationList.length; i++) {
        posttable.emailList.push(this.operationList[i].email);
      }
      this.$ajax
        .post("/userManagement?operation=freeze", posttable)
        .then(function(response) {
          return response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      this.reqAllUser("/userManagement");
      this.done("");
    },
    userDelete: function() {
      var posttable = { emailList: [] };
      for (var i = 0; i < this.operationList.length; i++) {
        posttable.emailList.push(this.operationList[i].email);
      }
      this.$ajax
        .post("/userManagement?operation=delete", posttable)
        .then(function(response) {
          return response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      this.reqAllUser("/userManagement");
      this.done("");
      console.log(this.operationList);
    },
    userAdd: function() {
      console.log(this.operationList);
    },
    setAuthority: function() {
      this.authorityDialog = true;
    },
    handleSet: function() {
      if (this.desAuthority === "") {
        this.fail("");
      } else {
        var auTable = { emailList: [], authority: this.desAuthority };
        for (var i = 0; i < this.operationList.length; i++) {
          auTable.emailList.push(this.operationList[i].email);
        }
        this.$ajax
          .post("/userManagement?operation=update", auTable)
          .then(function(response) {
            return response.data;
          })
          .catch(function(error) {
            console.log(error);
          });
        this.done("");
        this.reqAllUser("/userManagement");
        console.log(this.operationList);
        this.authorityDialog = false;
      }
    },
    reqAllUser: function(url) {
      var self = this;
      this.$ajax
        .get(url)
        .then(function(response) {
          var body = response.data;
          var tempUserTable = [];
          for (var i = 0; i < body.data.length; i++) {
            var ta = "";
            var iden = "";
            var au = body.data[i].authority;
            if (au === 1) {
              ta = "";
              iden = "";
            } else if (au === 2) {
              ta = "";
              iden = "";
            } else if (au === 3) {
              ta = "";
              iden = "";
            } else if (au === 4) {
              ta = "";
              iden = "";
            } else if (au === 0) {
              ta = "";
              iden = "";
            }
            tempUserTable.push({
              name: body.data[i].name,
              email: body.data[i].email,
              tag: ta,
              state: iden
            });
          }
          self.userTable = tempUserTable;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    done: function(info) {
      this.$message({
        message: info,
        type: "success"
      });
    },
    fail: function(info) {
      this.$message({
        message: info,
        type: "warning"
      });
    }
  }
};
</script>

<style scoped>
.all-user {
  argin-left: 5px;
  margin-right: 5px;
  padding: 25px;
  text-align: left;
}
</style>



